<template>
<v-container>
  <v-card>
    <v-card-title>
      <v-icon size="36" class="mx-2">{{ collectionOptions.icon }}</v-icon>{{ collectionOptions.title }}
      <v-spacer></v-spacer>
       <v-tooltip bottom v-if="collectionOptions.import">
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon @click="importFile" v-bind="attrs"
          v-on="on"> <v-icon>mdi-database-import </v-icon></v-btn>
        </template>
        <span>Import</span>
      </v-tooltip>
      <v-tooltip bottom v-if="collectionOptions.export">
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon @click="dialog = true" v-bind="attrs"
          v-on="on"> <v-icon>mdi-database-export </v-icon></v-btn>
        </template>
        <span>Export</span>
      </v-tooltip>
      <v-btn   color="blue white--text" class="ml-6" v-if="collectionOptions.assign && !isWakel && permissions.update.value" :disabled="selected.length < 1" @click="assignItems"  >
        <v-icon>mdi-clipboard-account</v-icon>
        Assign To
      </v-btn> 
      <v-btn   color="red white--text" class="ml-6" v-if="collectionOptions.withdraw && !isWakel && permissions.write.value"  @click="$router.push('/'+$route.params.collection+'/new?type=wtd')">
        <v-icon>mdi-bank-transfer-out</v-icon>
        Withdraw
      </v-btn> 
      <v-btn  v-if="!isWakel && permissions.write.value" color="success" class="ml-6"  @click="$router.push('/'+$route.params.collection+'/new')">
        <v-icon>mdi-plus</v-icon>
        New
      </v-btn> 
    </v-card-title>
    <v-card-text >
      <v-row>
        <v-col sm="4" v-for="(field, index) in filters" :key="index" >
        
          <component  class="mx-1"
                          :is="inputs[field.input]" 
                          v-bind="field.bind"
                          v-on="field.on"
                          outlined rounded dense hide-details
                          :value="setFilterValue($route.query[field.name] || null, field)"
                          @input="filter(field,$event)"
                          :ref="field.name"
                      />
        </v-col> 
      </v-row>
    </v-card-text>
  </v-card>



<!-- Total  -->
 
<v-sheet v-if="collectionOptions.total && total" class="pa-3" elevation="1">
  <v-simple-table>
    <thead>
      <tr>
        <th v-if="total.subtotal">Subtotal SAR</th>
        <th v-if="total.withdraw">Withdraw SAR</th>
        <th>Total SAR</th>
        <th>Total USD</th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <td v-if="total.subtotal"> {{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'SAR'  }).format(total.subtotal.amount) }}</td>
        <td v-if="total.withdraw"> {{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'SAR'  }).format(total.withdraw.amount) }}</td>
        <td> {{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'SAR'  }).format(total.amount) }}</td>
        <td> {{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD'  }).format(total.amount / 3.9) }}</td>
      </tr>
    </tbody>
  </v-simple-table>
</v-sheet>

 
  <v-alert icon="mdi-information-outline"   type="info" dense class="mt-4" v-if="selected.length > 0">
    <v-row align="center">
        <v-col class="grow pa-0">
        {{selected.length}} items is selected
        </v-col>
        <v-col class="shrink pa-0 px-2">
          <v-btn small @click="selected = []">unselect all</v-btn>
        </v-col>
      </v-row>
    
    
  </v-alert>
  <v-data-table  
        :loading="loading"
   
        class="my-5 elevation-1"
        :headers="headers"
        :items="items"
        hide-default-footer
        @update:options="tableOptions"
        multi-sort
        :single-expand="true"
        :expanded.sync="expanded"
        :show-expand="Boolean(collectionOptions.expanded)"
        :show-select="collectionOptions.select" 
        item-key="_id"
        v-model="selected"
     
      >
        <!-- @update:sort-desc="sortDesc" -->
      
     
      <template v-slot:item.amount="{ item }" >
        
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: collectionOptions.currency || 'USD',  }).format(item.amount) }}  
      </template>
        <template v-slot:item.ftd.amount="{ item }" >
   
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: collectionOptions.currency || 'USD',  }).format(item.ftd ? item.ftd.amount : 0) }}  
      </template>
      <template v-slot:item.active="{ item }" class="center-text text-center"  >
      <v-badge :color="item.active ? 'green' : 'grey'"></v-badge>  
      </template>
      <template v-slot:item.read="{ item }" class="center-text text-center"  >
      <v-badge :color="item.read ? 'green' : 'grey'"></v-badge>  
      </template>
      <template v-slot:item.created_on="{ item }"  >
        {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
      </template>
      <template v-slot:item.ftd.created_on="{ item }"  >
        {{ item.ftd ? dateformat(new Date(item.ftd.created_on), 'yyyy-mm-dd') : '--' }}
      </template>
      <template v-slot:item.country="{ item }"  >
        {{ countries.filter(c => c.code == item.country)[0].name }}
      </template>
      <template v-slot:item.type="{ item }"  >
       
        <v-icon :color="item.type == 'wtd' ? 'red' : 'green'" v-if="collection != 'payment_method'">
          {{ item.type == 'wtd' ? 'mdi-bank-transfer-out' : 'mdi-bank-transfer-in' }}
        </v-icon>
        <v-icon  v-if="collection == 'payment_method'" size="20">
          {{ item.type == 'wire' && collection == 'payment_method' ? 'mdi-bank' : 'mdi-credit-card' }}
        </v-icon>
      </template>


      <!-- Customers START -->


      <template v-slot:item.ftd[0].amount="{ item }"  >
 
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: collectionOptions.currency || 'USD',  }).format(item.ftd[0] ? item.ftd[0].amount : 0) }}  

      </template>
      <template v-slot:item.ftd[0].created_on="{ item }"  >
        {{ item.ftd[0] ? dateformat(new Date(item.ftd[0].created_on), 'yyyy-mm-dd') : '--' }}
      </template>
      <template v-slot:item.total[0].amount="{ item }"  >
 
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: collectionOptions.currency || 'USD',  }).format(item.total[0] ? item.total[0].amount : 0) }}  

      </template>
       <template v-slot:expanded-item="{ headers, item }" v-if="collectionOptions.expanded">
        <td :colspan="collectionOptions.table.length" class="pa-4">
          <v-data-table :headers="collectionOptions.expanded.headers" :items="item.child" >
            <template v-slot:item.amount="{ item }" >
                {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
            </template>   
   
            <template v-slot:item.created_on="{ item }"  >
              {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
            </template>
            
        </v-data-table>
      </td>
    </template> 
      <!-- Customers END  -->


      <template v-slot:item.actions="{ item }" >
        <v-tooltip bottom >
        <template v-slot:activator="{ on, attrs }">
        <v-icon
          v-if="permissions.write.value"
          v-bind="attrs" small
            v-on="on"
          class="mr-2"
          @click="cloneItem(item)"
        >
          mdi-content-copy
        </v-icon>
        </template>
        <span>Copy</span>
      </v-tooltip>
        <v-tooltip bottom >
        <template v-slot:activator="{ on, attrs }">
        <v-icon
          v-if="permissions.update.value"
          v-bind="attrs" small
            v-on="on"
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        </template>
        <span>Edit</span>
      </v-tooltip>
      <v-tooltip bottom >
        <template v-slot:activator="{ on, attrs }">
        <v-icon
          v-if="permissions.delete.value"
          v-bind="attrs" small
            v-on="on"
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
        </template>
        <span>Delete</span>
      </v-tooltip>
        
 

      </template>
      <template v-slot:loading   >
            <v-skeleton-loader
            class="mt-6"
            height="94"
            type="list-item-three-line"
          >
          </v-skeleton-loader>
      
      </template>

      <template v-slot:footer>
          
         <v-pagination
           class="pa-4" 
          :value="Number($route.query.page) || 1"
          @input="page"
          :length="totalPages"
          :total-visible="10"
          circle
        ></v-pagination>
      </template>
      </v-data-table>



 



  <v-dialog v-model="assign.dialog"  max-width="650" scrollable>
    <assign-select v-if="assign.dialog"
    :items="selected" 
    :headers="assign.headers"
    @close="assign.dialog = false"
    @save="assignToSave"
    @unselect="unselect"
    ></assign-select>

  </v-dialog>

</v-container>
</template>


<script>
import collections from './../collections'
import { mapActions } from 'vuex'
import dateformat from 'dateformat'
 
 import   {
  VTextField,
  VSelect,
  VSwitch,
} from 'vuetify/lib'
 
import AppAutoComplete from './../components/Autocomplete'
import AppDatePicker from './../components/DatePicker'
import AppDateRange from '../components/DateRange'
import AssignSelect from '../components/AssignSelect'
import countries from './../country'
 
import $ from 'jquery'

import debounce from 'debounce'
 
export default {
  name: 'Items',
  components: {
    AssignSelect
  },
  data () {
    return {
      selected: [],
      dialog: false,
      assign: {
        user: null,
        item: null,
        dialog: false,
        headers: [
          { text: 'TP Account', value: 'tpc' },
          { text: 'Account Name', value: 'name' },
          { text: 'Remove', value: 'action' },

        ]
      },
      countries,
      dateformat,
      collectionOptions: {},
      loading: false,
      items: [],
      inputs: {
        text: VTextField,
        select: VSelect,
        switch: VSwitch,
        date: AppDatePicker,
        range: AppDateRange,
        autocomplete: AppAutoComplete,
      },
      
      totalPages: 1,
      total: null,
      expanded: []
    }
  },
  computed: {
    user () {
      return this.$store.state.auth.user
    },
    isWakel () {
      if (this.user)
        return this.user.role == 'wakel'
      return false
    },
    filters () {
      if (this.collectionOptions.filters)
        return this.collectionOptions.filters.filter(input => {
          if (typeof input.if == 'function') {
            return input.if(this.$store.state)
          }
          return true
        })
      return false
    },
    headers () {
      const { fields } = this.permissions
      if( fields ) {
        const filteredFields = []
        Object.keys(fields).forEach(f => {
          if (fields[f])
            filteredFields.push(atob(f))
        })
        return this.collectionOptions.table.filter(e => filteredFields.indexOf(e.value) !== -1 || (e.value == 'actions' && (this.permissions.write.value || this.permissions.update.value || this.permissions.delete.value) ) )
      } else
        return this.collectionOptions.table
    },
    collection () {
      return this.$route.params.collection
    },
    permissions () {
      if (this.user) {
        const rights = this.user.rights
        const permissions = rights.filter(el => el.collection == this.collection)
        return permissions[0]
      } else return {}
    }
  },
  watch :{
    
    '$route': {
      handler: 'updateComponent',
      immediate:true
    } 
  },
  methods: {
    ...mapActions({
      get: 'api/get',
      update: 'api/update',
      remove: 'api/remove'
    }),
    assignItems () {
      this.assign.dialog = true
    },
    unselect (item) {
      this.selected = this.selected.filter(e => e != item)
    },
    async assignToSave (item) {
      if (this.selected.length) {
        const update = await this.update({ collection: 'customer', id: 'assign', data: {
          user: item,
          items: this.selected.map(e => e._id)
        } })

        if (update.status == 200) {
          this.$store.commit('success', update.data.nModified + ' items has been assigned')
          this.assign.dialog = false
          this.selected = []
          this.fetchData()
        } 
        
      } else {
        this.$store.commit('error', 'No item has been selected! please select at least one item ')
      }
     
    },
  
  //////////////

    importFile () {
      //  const input = document.createElement('input')
      //  input.type = 'file'
      //  input.onchange = (event) => {
      //    console.log(event.target.files);
      //    const reader = new FileReader();
         
      //    input.remove()
      //  }
      //  input.click()
    },
    async fetchData() {
      let { collection } = this
      
      this.items = []
      this.loading = true
      const result = await this.get({ collection: collection + window.location.search })
       
      if (this.collectionOptions.total) {
        const total = await this.get({ collection: collection + '/total' + window.location.search })
      
        if (total && total.status == 200) {
          this.total = total.data[0]
        }
      }
    
      
      
      if (result && result.status == 200) {
        this.items = result.data.docs
        this.totalPages = result.data.totalPages
      }
  
      this.loading = false
    },
    updateComponent () {
      this.selected = []
      // check if collection options exists or go 404
     this.collectionOptions = collections.get(this.collection)
      if (!this.collectionOptions)
        this.$router.replace('./404')
      else {
     
        this.fetchData()
      }
    },
     
    // handel sort
   
    page (page) {
      const  query  = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
      query.page = page
      const uri =  '/' + this.collection + '?' + $.param(query)
      this.$router.push(uri)
    },
    tableOptions (options) {
      const  query  = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
      // avoid pushing items to the sort fields
      if (query['sortBy[]'])
        delete query['sortBy[]']

      if (query['sortDesc[]'])
        delete query['sortDesc[]']

      query.sortBy = options.sortBy
      query.sortDesc = options.sortDesc
      
      const uri =  '/' + this.collection + '?' + $.param(query)
      this.$router.replace(uri).catch(() => {}) 
    },
    setFilterValue (value, field) {
      if (field.name == 'created_on' && value)
        value = value.split('|')

      if (field.input == 'text' && value)
          value = value.replace('~', '')
      

      return value
    },
    filter: debounce(function (field, value) {
        
        if (field.name == 'created_on' && Array.isArray(value))
          value = value.join('|')

        if (field.input == 'text')
          value = '~'+value

        
        const query = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
        if (!value && query[field.name])
          delete query[field.name]
        else
          query[field.name] = value
        
        query.page = 1
  
        const uri =  '/' + this.collection + '?' + $.param(query)
  
        this.$router.replace(uri) 

       
    
         
      
    }, 500),
   
    // handel item actions

    editItem (e) {
        this.$router.push('/'+this.$route.params.collection+'/'+e._id)
      },
    cloneItem (item) {
      this.$router.push('/'+this.$route.params.collection+'/new?clone=' + item._id)
    },
    async deleteItem (e) {
      const res = await this.$confirm('Are you sure do you want to delete this item ?', { title: 'Warning' })
      if (res) {
          this.loading = true
          const rm = await this.remove( { collection: this.$route.params.collection, id: e._id  } ) 
          if (rm.status === 200) {

            this.fetchData()
            this.loading = false
            this.$store.commit('success', 'Item deleted successfuly')
          }


      }
    },
  },
  mounted () {
    window.$items = this
  }
}
</script>